<style scoped>
.demo {
    width: 100%;
    height: 800px;
    position: relative;
}
.img1 {
    width: 500px;
    height: 300px;
    position: absolute;
    right: 0;
    top: 100px;
    background: rebeccapurple;
    transform-origin: center;
    animation: donghua1 10s infinite;
}
.img2 {
    width: 500px;
    height: 50px;
    position: absolute;
    background: rebeccapurple;
    left: 0;
    top: 30%;
    transform-origin: center;
    animation: donghua2 3s infinite;
}
.img3 {
    width: 500px;
    height: 50px;
    position: absolute;
    background: rebeccapurple;
    left: 0;
    bottom: 0;
    transform-origin: center;
    animation: donghua3 5s infinite;
}
.img4 {
    width: 200px;
    height: 80px;
    position: absolute;
    background: rebeccapurple;
    right: 300px;
    bottom: 200px;
    transform-origin: bottom;
    animation: donghua4 20s infinite;
}
.img5 {
    width: 80px;
    height: 200px;
    position: absolute;
    background: rebeccapurple;
    right: 500px;
    bottom: 200px;
    transform-origin: center;
    animation: donghua5 20s cubic-bezier(0.25, 0.1, 0.25, 1) infinite;
}
/* 船 */
@keyframes donghua1 {
    0% {
        transform: translateX(0);
    }
    50% {
        transform: translateX(10px);
    }
    10% {
        transform: translateX(0);
    }
}
/* 丝带 */
@keyframes donghua2 {
    0% {
        transform: translateX(0) translateY(0);
    }
    50% {
        transform: translateX(100px) translateY(80px);
    }
    10% {
        transform: translateX(0) translateY(0);
    }
}
/* 孔明灯 */
@keyframes donghua3 {
    0% {
        transform: translateX(0) translateY(0);
    }
    50% {
        opacity: 1;
    }
    100% {
        transform: translateX(400px) translateY(-80px);
        opacity: 0;
    }
}
/* 桥上的雨伞 */
@keyframes donghua4 {
    0% {
        transform: rotate(0);
    }
    5% {
        transform: rotate(10deg);
    }
    10% {
        transform: rotate(-10deg);
    }

    20% {
        transform: rotate(0);
    }

    30% {
        transform: rotate(0);
    }
    40% {
        transform: translateY(-15px) translateX(10px) scaleX(1.1);
    }
    45% {
        transform: translateY(0) translateX(20px) scaleX(1);
    }
    60% {
        transform: translateY(0) translateX(20px) scaleX(1);
    }
    70% {
        transform: translateY(0) translateX(20px) scaleX(1);
    }
    72% {
        transform: translateY(-15px) translateX(30px) scaleX(1.1);
    }
    74% {
        transform: translateY(0) translateX(40px) scaleX(1);
    }
    76% {
        transform: translateY(-15px) translateX(50px) scaleX(1.1);
    }
    76% {
        transform: translateY(0) translateX(60px) scaleX(1);
    }
    78% {
        transform: translateY(-15px) translateX(70px) scaleX(1.1);
    }
    80% {
        transform: translateY(0) translateX(80px) scaleX(1);
    }
    100% {
        transform: translateY(0) translateX(80px) scaleX(1);
    }
}
/* 剑客 */
@keyframes donghua5 {
    0% {
        transform-origin: bottom;
        transform: translateX(5px);
    }
    10% {
        transform-origin: bottom;
        transform: translateX(-5px);
    }
    20% {
        transform-origin: top;
        transform: translateX(5px);
    }
    30% {
        transform-origin: top;
        transform: translateX(-5px);
    }
    40% {
        transform-origin: bottom;
        transform: translateX(5px);
    }
    50% {
        transform-origin: bottom;
        transform: translateX(-5px);
    }
    60% {
        transform-origin: top;
        transform: translateX(5px);
    }
    70% {
        transform-origin: bottom;
        transform: translateX(-5px);
    }
    80% {
        transform-origin: bottom;
        transform: translateX(5px);
    }
    90% {
        transform-origin: bottom;
        transform: translateX(-5px);
    }
    100% {
        transform-origin: bottom;
        transform: translateX(-5px);
    }
}
</style>
<template>
    <div class="demo">
        <div class="img1">船</div>
        <div class="img2">丝带</div>
        <div class="img3">孔明灯</div>
        <div class="img4">桥上的雨伞</div>
        <!-- <div class="img5">剑客</div> -->
        <div class="img6"></div>
        <div class="img7"></div>
        <div class="img8"></div>
        <div class="img9"></div>
        <div class="img10"></div>
    </div>
</template>

<script>
export default {
    name: 'snowflake',
    data() {
        return {};
    },
    methods: {},
    created() {},
    mounted() {},
    beforeDestroy() {},
};
</script>